import React from 'react'
import styled from 'styled-components';
import metric from '../data/metric';
import { Bubble, Line } from 'react-chartjs-2';
import { Chart, registerables } from "chart.js"
Chart.register(...registerables)


const options = {
    radius : 0,
    borderWidth: 1,
    spanGaps: true,
    maxBarThickness: 30,
    grouped: true,
    responsive: false,
    interaction: {
        mode: 'index',
    },
    plugins: {
    legend: {
        labels: {
        usePointStyle: true,
        padding: 30,
        font: {
            family: "'Noto Sans KR', 'serif'",
            lineHeight: 3,
        },
        }
    }
  }
};


const Testchart = (props) => {
  let i=props.i
  return (
    <Container>
      <Line type="line" data={metric[i]} options={options} />
      {/* <Line type="line" data={metric[i]} options={options} style={{  width:"350px", height: "250px" }}/> */}
    </Container>
  );
};

export default Testchart;

const Container = styled.div`
  width: 25vw;
  max-width: 350px;
  height: 30vw;
  max-height: 280px;
`;