
import data1 from '../data/srdata'
import { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Row, Col} from 'react-bootstrap';
import '../App.css';

function Portallist() {
    let [data] = useState(data1);
  return (
    <div>
      <Container>
      <Row>
            <Col xxl="3">SR</Col>
            <Col xxl="3"></Col>
            <Col xxl="6"></Col>
            {/* <Col>{props.data[i].startday}</Col> */}
        </Row>
          
         {
              data.map(function(a,i)
              {
                if(i<15){
                  return(
                    <DataMap data={data} i={i} key={i}/>
                  )
                } 
                return null;
              })
          }
      </Container>
    </div>
  )
}

function DataMap(props){
  let i = props.i;
  return(
          <Row>
            <Col xxl="3">{props.data[i].sr_id}</Col>
            <Col xxl="3">{props.data[i].type}</Col>
            <Col xxl="6">{props.data[i].title}</Col>
            {/* <Col>{props.data[i].startday}</Col> */}
          </Row>
          )
}

export default Portallist