import srdata from '../data/srdata'
import React, { useState } from "react";
import '../App.css';
import Pagination from "react-js-pagination";
import { useNavigate} from 'react-router-dom';
import { Container, Row, Col } from 'react-bootstrap';

const Paging = () => {
  const [data, setData] = useState(srdata);
  const [page, setPage] = useState(1);
  // const [items, setItems] = useState(20);

  const handlePageChange = (page) => { setPage(page); };
  // const itemChange = (e) => {
  //   setItems(Number(e.target.value))

  // }
  // console.log(items*(page-1), items*(page-1)+items);
  let navigate = useNavigate();

  return (
    <div>
      {/* <div>
        <select name="items" onChange={itemChange}>
          <option value="5">5</option>
          <option value="10">10</option>
          <option value="15">15</option>
          <option value="20">20</option>
        </select>
      </div> */}
     

      <Container>
             
        <Row>
          <Col xl="1"></Col>
          <Col xl="2"></Col>
          <Col xl="1"></Col>
          <Col xl="5"></Col>
          <Col xl="2"></Col>
          <Col xl="2"></Col>
        </Row>
         
      {data.slice(20*(page-1), 20*(page-1)+20
      ).map((v,i) => {
        return (
          <Row onClick={()=>{navigate('/service/detail/'+ v.id)}}>
            <Col xl="1">{v.sr_id}</Col>
            <Col xl="2">{v.type}</Col>
            <Col xl="1">{v.name}</Col>
            <Col id = 'td_left' xl="3">{v.title}</Col>
            <Col xl="1">{v.state}</Col>
            <Col xl="2">{v.startday}</Col>
            <Col xl="2">{v.endday}</Col>
        </Row>
        )
      })}
      </Container>

      <Pagination
        activePage={page}
        itemsCountPerPage={20}
        totalItemsCount={120}
        pageRangeDisplayed={5}
        prevPageText={""}
        nextPageText={""}
        onChange={handlePageChange} />
    </div>
  )
};

export default Paging;
