import { useState } from 'react';
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Routes, Route} from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import { Container, Row, Col } from 'react-bootstrap';
import Paging from './paging';
import Detail from './detail';
import axios from 'axios'


function Service() {
    //   . DB     
    let [data,setData] = useState([]);
    axios.get('https://violetleaf44.github.io/react/data/srdata.json').then((response)=>{
      setData(response.data);
        })
        .catch(()=>{
          console.log('failed')
        });
    //   

    const [search, setSearch] = useState("");
    const [search2, setSearch2] = useState("");
    const numChange=(e)=>{
      setSearch(e.target.value);
      console.log(e.target.value);
    }

    const nameChange=(e)=>{
      console.log(e.target.value);
    }
    const titleChange=(e)=>{
      console.log(e.target.value);
    }
    const OPTIONS = [
      { value: "", name: "-------------------------" },
      { value: "", name: "" },
      { value: "", name: "" },
      { value: "", name: "" },
    ];
    
    const SelectBox = (props) => {
      const typeChange = (e) => {
        setSearch2(e.target.value);
        console.log(e.target.value);
      };
    
      return (
        <select onChange={typeChange}>
          {props.options.map((option) => (
            <option
              key={option.value}
              value={option.value}
              defaultValue={props.defaultValue === option.value}
            >
              {option.name}
            </option>
          ))}
        </select>
      );
    };

  
  return (
    <div>
      <Container>    
        {/*  */}
        <Row>
          <Col xl="1"></Col>
          <Col xl="3"><input type="text" name="sr_num" value={search} onChange={numChange}></input></Col>
          <Col xl="1"></Col>
          <Col xl="3"><SelectBox options={OPTIONS}></SelectBox></Col>
          <Col xl="1"></Col>
          <Col xl="3"><SelectBox options={OPTIONS} onChange={nameChange}></SelectBox></Col>
        </Row>
        <Row>
          <Col xl="1"></Col>
          <Col xl="11"><input type="text" name="sr_title" onChange={titleChange} ></input></Col>
        </Row>
        <Row>
          <Col xl="1"></Col>
          <Col xl="3"><input type="text" name="sr_state"></input></Col>
          <Col xl="1"></Col>
          <Col xl="3"><input type="text" name="sr_laststate"></input></Col>
          <Col xl="1"></Col>
          <Col xl="3"></Col>
        </Row>
        <Row>
          <Col xl="1"></Col>
          <Col xl="3"><input type="text" name="sr_startday"></input></Col>
          <Col xl="1"></Col>
          <Col xl="3"><input type="text" name="sr_endday"></input></Col>
          <Col xl="4"></Col>
        </Row>
        <Row>
          <Col xl="1"><Button variant="dark"></Button></Col>
        </Row>
      </Container> 
      {/*   */}

      {/*   */}
      <Routes> 
        <Route path="/" element={<Paging />} />;
        <Route path="/detail/:id" element={<Detail data={data}/>} />;
        <Route path="*" element={<div>404  .</div>} /> 
      </Routes>
      {/*   */}

    </div>
      );
}

